<template>
	<span>
		<router-script src="./static/js/jquery.jedate.min.js"></router-script>
		<input :id="jedataId" type="text">
	</span>
</template>
<style>
	
</style>
<script>
	import 'root/css/jedate.css';
	import routerScript from './routerScript.vue';
	
	export default {
		components: {
			routerScript
		},
		created() {

		},
		data() {
			return {
				jedataId: 'jedata' + ~~(Math.random() * 99999)
			}
		},
		mounted() {
		},
		methods: {

		}
	}
</script>


